import * as React from 'react';
import { useContext, useState, useEffect } from 'react';

import noteContext from './NoteContext';


import API from "./API";





export default function Report() {




    const [MasterClassesGrades, setMasterClassesGrades] = useState([ ]);



 
    
        const data = useContext(noteContext);
    
    
    
    
    
    
        let token = data.token;
    
    
    
    
        const fetchClassGrades = async () => {
    
            try {
                const res = await API.get("/hw/hw_report_links", {
                    headers: {
                        'content-type': 'text/json', Authorization: 'Bearer ' + token
                    }
                });
                setMasterClassesGrades(res.data);
    
    
    
            }
            catch (error) {
    
                console.log(error);
    
    
            }
    
    
        };
    
   




    useEffect(() => {

        fetchClassGrades();



    }, []);






    return (

        <>


            Homework report

            <table className="table  table-auto  ">

                <thead>

                    <tr>


                        <th>
                            Class
                        </th>


                    </tr>
                </thead>
                <tbody>

                    {MasterClassesGrades.map((item, index) => (


                        <tr key={index}>

                            <td>


                                <a className="btn btn-success" target='_blank' href={`${item.link}`}>{item.class}</a>






                            </td>


                        </tr>
                    ))}
                </tbody>
            </table>




        </>

    )
}